import Tile from './tile';

export default class Anvil extends Tile {
    constructor(x, y, tileData) {
        super(x, y, tileData);

        this.isInteractive = true;
        this.firstTouch = true;
    }

    touch(game) {
        if (this.firstTouch) {
            this.firstTouch = false;
            game.ui.showDialog(" \n\n     ", () => {
                game.ui.showCombine();
            });
        } else {
            game.ui.showCombine();
        }
    }
}



